.gop-gitops-resources__title {
  font-weight: 400;
  font-size: 16px;
  margin: 0px;
  padding: 1.5rem;
  align-items: center;
}

.gop-gitops-resources__list {
  display: flex;
  max-width: 203px;
}

.gop-gitops-resources .pf-v5-l-split.pf-m-gutter {
  justify-content: space-between;
}

.gop-gitops-resources .pf-v5-l-split.pf-m-gutter>*:not(:last-child) {
  margin-right: var(--pf-v5-global--spacer--sm);
}

